const state = () => {
  return {
    currentUser: {
      UserDomain: null,
      UserName: null,
      NickName: null,
      HeadImg: null,
      UserSign: null
    },
    macAddress: null,
    Visitor: null
  }
}

const mutations = {
  setVisitor(state, val) {
    state.Visitor = val || null
  },
  setUser(state, val) {
    state.currentUser = val || null
  },
  setMacAddress(state, val) {
    state.macAddress = val || null
  }
}

const getters = {
  isAuthenticated(state) {
    //!!
    //oflag null/undefined/0""/testo.flagtestfalse nullundefined
    return !!state.currentUser
  },
  VisitorInfo(state) {
    return state.Visitor
  },
  currentUser(state) {
    return state.currentUser
  },
  macAddress(state) {
    return state.macAddress
  }
}

const actions = {}
export default {
  state: state,
  mutations: mutations,
  getters: getters,
  actions: actions
}



