<template>
  <section>
    <form class="form-horizontal" autocomplete="off" @submit.prevent="submitForm">
      <div class="p-3">
        <mu-avatar slot="avatar" :src="kefuData.headimage"/>
        <mu-raised-button label="" icon="folder" background-color="#ba68c8" class="align-middle">
          <vue-base64-file-upload
            :max-size="customImageMaxSize"
            :disable-preview="true"
            class="v1"
            accept="image/png, image/jpeg"
            image-class="v1-image"
            input-class="v1-file"
            @size-exceeded="onSizeExceeded"
            @file="onFile"
            @load="onLoad"
          />
        </mu-raised-button>
        <mu-divider/>
        <mu-text-field v-validate="'required'" v-model="kefuData.nickname" :error-text="errors.has('')?errors.first(''):''" label="" hint-text="" name="" icon="location_city" label-float/>
        <mu-text-field
          v-validate="'required|min:6'"
          v-if="isPawword"
          v-model="kefuData.oldPassword"
          :error-text="errors.has('')?errors.first(''):''"
          label=""
          hhint-text=""
          name=""
          icon="lock"
          label-float
          type="password"
        />
        <mu-text-field
          v-validate="'min:6'"
          v-else
          v-model="kefuData.oldPassword"
          :error-text="errors.has('')?errors.first(''):''"
          label=""
          hhint-text=""
          name=""
          icon="lock"
          label-float
          type="password"
        />
        <mu-text-field
          v-validate="'min:6'"
          ref=""
          v-model="kefuData.password"
          :error-text="errors.has('')?errors.first(''):''"
          label=""
          hhint-text=""
          name=""
          icon="lock"
          label-float
          type="password"
        />
        <mu-text-field
          v-validate="'min:6|confirmed:'"
          v-model="kefuData.confirmPassword"
          :error-text="errors.has('')?errors.first(''):''"
          label=""
          hhint-text=""
          name=""
          icon="lock"
          label-float
          type="password"
        />
        <p class="ml-4">
          <mu-flat-button slot="actions" primary label="" type="submit"/>
        </p>
      </div>
    </form>
  </section>
</template>
<script>
import VueBase64FileUpload from 'vue-base64-file-upload'
import axios from 'axios'
import { mapGetters, mapMutations } from 'vuex'
import tools from '@/utiltools/tools'
import { setCurrentUserToLocalStorage } from '@/utiltools/auth'
import { logout } from '@/utiltools/lock'
export default {
  middleware: 'authenticated',
  components: {
    VueBase64FileUpload
  },
  data() {
    return {
      kefuData: {},
      kefuTemp: {
        username: null,
        headimage: null,
        nickname: null,
        domain: null,
        sign: null,
        state: 1,
        oldPassword: null,
        password: null,
        confirmPassword: null
      },
      customImageMaxSize: 0.2,
      isPawword: false
    }
  },
  computed: {
    ...mapGetters(['isAuthenticated', 'currentUser', 'macAddress'])
  },
  watch: {
    kefuData: {
      handler(curVal, oldVal) {
        if (curVal.password !== null && curVal.password !== '')
          this.isPawword = true
        else this.isPawword = false
      },
      deep: true
    }
  },
  created() {
    var that = this
    that.reset()
  },
  methods: {
    onFile(file) {},
    onLoad(dataUri) {
      this.kefuData.headimage = dataUri
    },
    onSizeExceeded(size) {
      alert({
        title: '200K',
        icon: 'error'
      })
    },
    reset() {
      var that = this
      that.kefuTemp.username = that.currentUser.UserName
      that.kefuTemp.headimage = that.currentUser.HeadImg
      that.kefuTemp.nickname = that.currentUser.NickName
      that.kefuTemp.domain = that.currentUser.UserDomain
      that.kefuTemp.sign = that.currentUser.UserSign
      that.kefuData = JSON.parse(JSON.stringify(that.kefuTemp))
    },
    submitForm() {
      var that = this
      that.$validator
        .validateAll()
        .then(result => {
          if (result) {
            var that = this
            let url = tools.kefuUrl + 'Update'
            let postdata = {
              domain: that.kefuData.domain,
              username: that.kefuData.username,
              sign: that.kefuData.sign,
              sessionid: that.macAddress,
              ischpw: false,
              content: that.kefuData
            }
            if (that.kefuData.password != null && that.kefuData.password !== '')
              postdata.ischpw = true
            this.$httpform
              .post(url, JSON.stringify(postdata))
              .then(response => {
                var json = response.data
                if (json.error === 0) {
                  if (!postdata.ischpw) {
                    let user = {
                      UserDomain: that.kefuData.domain,
                      UserName: that.kefuData.username,
                      NickName: that.kefuData.nickname,
                      HeadImg: that.kefuData.headimage,
                      UserSign: that.kefuData.sign
                    }
                    setCurrentUserToLocalStorage(user)
                    that.$emit('back')
                  } else {
                    that.alertInfo(', ', response =>
                      logout()
                    )
                  }
                } else that.alertError(json.error + ':' + json.content)
              })
              .catch(error => {
                that.alertError('')
                console.log(error)
              })
          }
        })
        .catch(failure => {
          console.log(failure)
        })
    },
    alertInfo(val, event) {
      this.$emit('alertInfo', [val, event])
    },
    alertError(val) {
      this.$emit('alertError', val)
    }
  }
}
</script>
<style scoped>
.mu-avatar {
  margin: 0 20px;
  vertical-align: middle;
}
.mu-divider {
  margin: 10px;
}
.v1 {
  width: 120px;
  height: 100%;
  position: absolute;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  opacity: 0;
}

.v1-image {
  display: none;
}
</style>



